import {
  InsertStickerProps,
  MoveCaretProps,
  SetStickerProps,
  SetStickerVisibleProps,
  SetRangeProps,
} from './types';

export const STICKER_INSERT = Symbol('STICKER_INSERT');
export const STICKER_SET = Symbol('STICKER_SET');
export const STICKER_PANEL_SET_VISIBLE = Symbol('STICKER_PANEL_SET_VISIBLE');
export const STICKER_MOVE_CARET = Symbol('STICKER_MOVE_CARET');
export const STICKER_SET_RANGE = Symbol('STICKER_SET_RANGE');
export const INSERT_MARKDOWN_EMOJI = Symbol('INSERT_MARKDOWN_EMOJI');

export function insertSticker(payload: InsertStickerProps) {
  return {
    type: STICKER_INSERT,
    payload,
  };
}

export function setSticker(payload: SetStickerProps) {
  return {
    type: STICKER_SET,
    payload,
  };
}

export function setStickerPanelVisible(payload: SetStickerVisibleProps) {
  return {
    type: STICKER_PANEL_SET_VISIBLE,
    payload,
  };
}

export function moveCaret(payload: MoveCaretProps) {
  return {
    type: STICKER_MOVE_CARET,
    payload,
  };
}

export function setRange(payload: SetRangeProps) {
  return {
    type: STICKER_SET_RANGE,
    payload,
  };
}
export function insertMarkdownEmoji(payload) {
  return {
    type: INSERT_MARKDOWN_EMOJI,
    payload,
  };
}
